<template>
  <LayoutPage class="bg">
    <div class="font-size | max-w:500">
      <div class="box">
        <div class="box__title">View Font</div>
        <div class="box__content">
          <div class="font-demo">
            <span class="font-demo__text" :style="{
              fontSize: `${nowFontSize}px`,
              fontWeight: `${nowFontWeight}`,
            }">Font Size : {{ nowFontSize }}px</span>
          </div>
        </div>
      </div>

      <div class="box">
        <div class="box__title">Setting Font</div>
        <div class="box__content">
          <ul class="options">
            <li class="options__item">
              <div class="item">
                <div class="item__title">font-size : </div>
                <div class="item__content:input">
                  <div>
                    <input type="number" :min="1" :step="1" v-model="nowFontSize" @input="validateInput">
                  </div>
                  <div>px</div>
                </div>
              </div>
            </li>
            <li class="options__item">
              <div class="item">
                <div class="item__title">font-weight : </div>
                <div class="item__content:radio">
                  <ul class="weight-options">
                    <li class="radio-btn" v-for="item in fontWeightList" :key="item">
                      <input type="radio" :id="`weight${item.value}`" name="weight" :value=item.value
                        v-model="nowFontWeight">
                      <label :for="`weight${item.value}`">{{ item.name }}</label>
                    </li>
                  </ul>
                </div>
              </div>
            </li>
          </ul>

        </div>
      </div>
    </div>
  </LayoutPage>
</template>

<script setup>
import { defineOptions, ref } from 'vue'
import LayoutPage from '@/components/layout/page'

defineOptions({ name: 'DemoFontSize' })

const nowFontSize = ref(16)
const validateInput = (event) =>{
  const regex = /^[1-9]\d*$/
  if (!regex.test(event.target.value)) {
    nowFontSize.value = event.target.value.replace(/[^0-9]/g, "").replace(/^0+/, "");
  }
}

const nowFontWeight = ref('normal')

const fontWeightList = ref([
  {
    name: 'Normal',
    value: 'normal',
  },
  {
    name: 'ExtraLight/200',
    value: 200,
  },
  {
    name: 'Light/300',
    value: 300,
  },
  {
    name: 'Regular/400',
    value: 400,
  },
  {
    name: 'Medium/500',
    value: 500,
  },
  {
    name: 'SemiBold/600',
    value: 600,
  },
  {
    name: 'Bold/700',
    value: 700,
  },
  {
    name: 'ExtraBold/800',
    value: 800,
  },
  {
    name: 'Black/900',
    value: 900,
  },
])
</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style
</style>